import React from 'react';
import './styles.css';

const Navigation = ({links}) => (
  <ul className="Navigation">
    {links.map(link =>
      <li key={link} className="Navigation__item">
        <a className="Navigation__link" href="">{link}</a>
      </li>
    )}
  </ul>
);

export default Navigation;



