import React from 'react';
import './styles.css';

const WatchlistCard = ({
  img,
  title,
  date,
  rating,
  onDetachFromWatchlist}) => (
  <div className="WatchlistCard">
    <img
      className="WatchlistCard__img"
      src={`https://image.tmdb.org/t/p/w300${img}`}
      alt=""
    />
    <div className="WatchlistCard__body">
      <h3 className="WatchlistCard__title">{title}</h3>
      <p className="WatchlistCard__release">Released: {date}</p>
      <p className="WatchlistCard__rating">Rating: {rating}</p>
      <button
        className="WatchlistCard__btn"
        onClick={onDetachFromWatchlist}>-</button>
    </div>
  </div>
);

export default WatchlistCard;



