import React from 'react';
import { types } from 'api';
import './styles.css';

const SearchCategories = ({onSearch}) => (
  <div className="SearchCategories">
    <button
      className="SearchCategories__btn"
      onClick={() => {
        onSearch(types.POPULAR);
      }}
    >popular</button>
    <button
      className="SearchCategories__btn"
      onClick={() => {
        onSearch(types.TOP_RATED);
      }}
    >top rated</button>
    <button
      className="SearchCategories__btn"
      onClick={() => {
        onSearch(types.UPCOMING);
      }}
    >upcoming</button>
  </div>
);

export default SearchCategories;



