const API_URL = "https://api.themoviedb.org/3/";
const API_KEY = "f24a0fd18f52218851075901c5a108a0";

export const types = {
  POPULAR: "POPULAR",
  UPCOMING: "UPCOMING",
  TOP_RATED: "TOP_RATED"
};

export const getMoviesByName = name => {
  return fetch(`${API_URL}search/movie?api_key=${API_KEY}&query=${name}`)
    .then(response => {
      if (response.ok) {
        return response.json();
      }

      throw new Error("Error fetching data");
    })
    .then(data => data.results)
    .catch(err => console.error("Error: ", err));
};

export const getMoviesByCategory = category => {
  let url = "";

  switch (category) {
    case types.POPULAR:
      url = `${API_URL}movie/popular?api_key=${API_KEY}`;
      break;
    case types.TOP_RATED:
      url = `${API_URL}movie/top_rated?api_key=${API_KEY}`;
      break;
    case types.UPCOMING:
      url = `${API_URL}movie/upcoming?api_key=${API_KEY}`;
      break;
    default:
      break;
  }

  console.log(url);

  return fetch(url)
    .then(response => {
      if (response.ok) {
        return response.json();
      }

      throw new Error("Error fetching data");
    })
    .then(data => data.results)
    .catch(err => console.error("Error: ", err));
};



