import React from "react";
import MovieCard from "components/MovieCard";
import './styles.css';

const MovieList = ({ movies, onDeleteMovie, onAttachToWatchlist }) => (
  <div className="MovieList">
    {movies.length === 0
      ? <p className="MovieList__msg">Sorry, we didn't find anything <br/> (_)</p>
      : ''}
    {movies.map(movie => (
      <div
        className="MovieList__item"
        key={movie.id}
      >
        <MovieCard
          movie={movie}
          onDeleteMovie={() => {
            onDeleteMovie(movie.id);
          }}
          onAttachToWatchlist={() => {
            onAttachToWatchlist(movie.id)
          }}
        />
      </div>
    ))}
  </div>
);

export default MovieList;



